import React from "react";
import DialogItem from './DialogItem/DialogItem';
import classes from "./Dialogs.module.css";
import Message from './Messages/Message';
import { Field, reduxForm } from "redux-form";
import { InputUI } from "../common/FormsControls/FormsControls";
import { required, maxLengthCreator } from "../../validators";
import Button from "@material-ui/core/Button";



const Dialogs = (props) => {
    const dialogsElements = props.dialogsPage.dialogsData.map(dialog => (
      <DialogItem name={dialog.name} id={dialog.id} key={dialog.id} />
    ));
    const messageElements = props.dialogsPage.messageData.map(el => (
      <Message message={el.message} id={el.id} key={el.id} />
    ));

      let addNewMessage = (values) => {
        props.sendMessage(values.newMessageBody)
      }

      return (
        <div className={classes.dialogs}>
          <div className={classes.dialogItems}>{dialogsElements}</div>
          <div className={classes.messages}>
            <div>{messageElements}</div> 
            <div>
              <div>
                <AddMessageFormRedux onSubmit={addNewMessage} />
              </div>
            </div>
          </div> 
        </div>
      );
  }

 const MaxLength50 = maxLengthCreator(50)


      const AddMessageForm = (props) => {
        return <form className={classes.form} onSubmit={props.handleSubmit} autoComplete="off">
            <Field component={InputUI} name="newMessageBody" placeholder="Enter Your Message" validate={[required, MaxLength50]} />
            <div>
              <Button variant="contained" color="primary" className={classes.btn} type="submit">
                Send
              </Button>
            </div>
          </form>;  
    }

      const AddMessageFormRedux = reduxForm({
        form: "dialogMessageForm"
      })(AddMessageForm);
      


export default Dialogs;
